import React, { Component } from 'react'
import { connect } from "react-redux";
import { FiSettings, FiX } from "react-icons/fi";
import * as actions from '../actions';
import Gauge from '../components/visuals/Gauge';
import BasicGraph from '../components/visuals/BasicGraph';
import TextBox from '../components/visuals/TextBox';
import Map from '../components/visuals/Map';
import AdvancedGraph from '../components/visuals/AdvancedGraph';
import TileType from '../components/visuals/TileType';
import Table from '../components/visuals/Table';
import CustomTable from '../components/visuals/CustomTable';
import CylinderGauge from '../components/visuals/CylinderGauge';
import SolidGauge from '../components/visuals/SolidGauge';
import IframeTile from '../components/visuals/IframeTile';

const components = {
  TileType: TileType,
  Gauge: Gauge,
  BasicGraph: BasicGraph,
  AdvancedGraph: AdvancedGraph,
  TextBox: TextBox,
  Map: Map,
  Table: Table,
  CustomTable: CustomTable,
  CylinderGauge: CylinderGauge,
  SolidGauge: SolidGauge,
  IframeTile: IframeTile
}

class Tile extends Component {
  handleEditTile(tile) {
    const { toggleModal } = this.props;
    toggleModal(tile);
  }
  render () {
    const { tile, deleteTile, activeTab, ui  } = this.props;
    const TagName = tile.properties.type && components[tile.properties.type.value] ? components[tile.properties.type.value] : components['TileType'];
    const viewOnly = ui.viewOnly ? 'none' : '';
    return ( < >
      <div className='tile' style={{height: '100%', width: '100%'}}>
        <span
          className='tile-remove'
          style={{position: 'absolute', top: '5px', right: '10px', zIndex: 1000, display: viewOnly }}
          title="Click to delete tile"
          onClick={() => deleteTile(tile.id, activeTab)}
        ><FiX style={{height:'1rem'}}/>
        </span>
        <span
          style={{position: 'absolute', top: '5px', left: '10px', zIndex: 1000, cursor: 'pointer', display: viewOnly }}
          title="Click to Edit tile"
          onClick={(e) => this.handleEditTile(tile)}
        ><FiSettings style={{height:'1rem'}} size={'1rem'} />
        </span>
        <TagName tile={tile} />
      </div>
    </> )
  }
}

function mapStateToProps({ activeTab, ui }) {
  return { activeTab, ui };
}

export default connect(mapStateToProps,actions)(Tile);
