import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Editor } from 'primereact/editor';

// import '../../../node_modules/quill/dist/quill.core.css';
// import '../../../node_modules/quill/dist/quill.bubble.css';
// import '../../../node_modules/quill/dist/quill.snow.css';


class TextBoxForm extends Component {
  state = {
    text: this.props.ui.modal.tile ? this.props.ui.modal.tile.properties.text : '',
    edit: false
  }

  componentDidMount() {
    // populates fields when editing
    //const { tile } = this.props.ui.modal;
    // if(tile) {
    //   const { text } = tile.properties;
    //   this.setState({text, edit:true});
    // }
  }

  handleChange = (e) => {
    const {addData} = this.props;
    this.setState({text: e.htmlValue}, () =>{
    addData(this.state);
    });
  }

  render () {
    const { text, edit } = this.state;
    return (
      <section className='component-form' >
        <Editor
          style={{height:'200px'}}
          name='text'
          value={text}
          onTextChange={this.handleChange}
          placeholder='Your text here'
        />
      {edit && <p style={{marginTop:0}}>Note: Text in the editior can only be overwritten, not edited.</p>}
      </section>
    )
  }
}

function mapStateToProps({ui}) {
  return {ui}
}

export default connect(mapStateToProps)(TextBoxForm);
