const data = (state={}, action) => {
  switch(action.type) {
    case 'CURRENT_DATA': {
      if(action.payload.status === 200) {
        return {
          ...state,
          dataItems: action.payload.data
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          dataItems: null
        };
      }
    }
    case 'ASSET_DATA': {
      if(action.payload.status === 200) {
        return {
          ...state,
          asset: action.payload.data
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          asset: null
        };
      }
    }
    case 'ASSET_DEFINITION_DATA': {
      if(action.payload.status === 200) {
        return {
          ...state,
          assetDef: action.payload.data
        };
      } else {
        console.log(action.payload)
        return {
          ...state,
          assetDef: null
        };
      }
    }
    default:
      return state;
  }
}

export default data;
